.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.clearfix { display: inline-block; }
* html .clearfix { height: 1%; }
.clearfix { display: block; }

html,
body {
  height: 100%;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: $mainFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  * {
    box-sizing: border-box;
  }
  background-color: $dark;
  font-size: 1rem;
  color: $fontColor;  
}

#root {
	display: block;
	width: 100%;
	min-height: 100%;
}


.portfolio-main_container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  
  @media (min-width: 769px) {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 100px 20px;
	}  
}

.header-sidebar {
  padding-bottom: 40px;
  border-bottom: 1px solid $mediumDark;
  margin-bottom: 20px;
  @media (min-width: 769px) {
    width: 50%;
    border-bottom: none;
	}
}

.main_content {
  @media (min-width: 769px) {
    width: 50%;
	}
  > section {
    line-height: 1.5rem;
    margin-bottom: 5rem;
    h2 {
      color: rgba($color: $white, $alpha: .8);
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    p {
      margin-bottom: 1rem;
    }
    @media (min-width: 769px) {
      padding: 0 1rem;
    }
  }
}

.highlight {
  color: rgba($color: $white, $alpha: .9);
  font-weight: 500;
}

a {
  text-decoration: none;
}
