/* EXPERIENCE */

.experience_content {
  h2 {
    
  }
  @media (min-width: 769px) {
    
    
	}
}

.experience_list,
.technologies_list,
.projects_list {
  @include resetList;
}

.experience_item { // li
  margin: 1rem 0;
}

.item_box {
  padding: 1rem 0;  
  @include border-radius(5px);
  &:hover {
    background-color: rgba($color: $black, $alpha: .1);
  }
  .experience_date {
    margin-bottom: 1rem;
  }
  
  @media (min-width: 769px) {
    padding: 1rem;
    display: flex;
    .experience_date {
      width: 150px;
      padding-right: 20px;
    }
    
    .experience_info {
      width: calc( 100% - 150px);
    }  
	}
}


.experience_title {
  .experience_position {
    font-size: 1.2rem;
    color: rgba($color: $white, $alpha: .9);
  }
  .experience_company {
    font-size: 1.1rem;
    color: rgba($color: $white, $alpha: .7);
  }
}

.projects_list {
  .projects_list-item {
    margin-top: 1.5rem;
  }
  
  .project_name {
    font-size: 1.1rem;
    color: rgba($color: $white, $alpha: .7);
  }
  .projects_links {
    padding: 1rem 0 ;
    list-style-type: disc;
    list-style-position: inside;
    li {
      font-size: .9rem;
      color: $alternateLight;
      margin-bottom: .2rem;
      a {
        color: $alternateLight;
      }
    }
  }
}

.technologies_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;  
  padding-top: 20px;
}

.technologies_list-item {
  color: $alternateLight;
  background-color: rgba($color: $black, $alpha: .3);
  padding: .4rem .8rem;
  font-size: .8rem;
  line-height: 1rem;
  @include border-radius(50px);
  margin:0 .8rem 1rem 0;
  font-weight: 500;
}


.item_box-alternate {
  border: 1px solid rgba($color: $white, $alpha: .5);
  @include border-radius(8px);
  padding: 1rem;
  margin-top: 1rem;
}

