.header_sidebar-contents {
	@media (min-width: 769px) {
		position: fixed;
		padding: 0 1rem;	
	}	
}

.header-sidebar {	
	h1 {
		font-size: 2rem;
		font-weight: 700;
		margin-bottom: 2rem;
		
		a {
			color: rgba($color: $white, $alpha: .8);;
		}
	}
	h2 {
		margin-bottom: 1rem;
		font-size: 1.2rem;
		line-height: 1.5rem;
		color: rgba($color: $white, $alpha: .7);
	}
	.header-tagline {
		font-size: 1rem;
		line-height: 1.5rem;
		color: rgba($color: $white, $alpha: .5);
	}
	
	li {
		@include resetList;
	}
	@media (min-width: 769px) {
		h1 {
			font-size: 3rem;
		}
		h2 {
			font-size: 1.5rem;
		}
	}

}

.header_navigation {
	display: none;
	@media (min-width: 769px) {
		display: block;	
	}
	ul {
		margin-top: 4rem;
	}
	li {		
		padding: .8rem 0;
		a {
			color: rgba($color: $white, $alpha: .7);
			&:hover {
				color: rgba($color: $white, $alpha: .9);
			}
		}
	}
}