.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: "Roboto", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #28025A;
  font-size: 1rem;
  color: rgba(239, 239, 239, 0.5);
}
body * {
  box-sizing: border-box;
}

#root {
  display: block;
  width: 100%;
  min-height: 100%;
}

.portfolio-main_container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}
@media (min-width: 769px) {
  .portfolio-main_container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 100px 20px;
  }
}

.header-sidebar {
  padding-bottom: 40px;
  border-bottom: 1px solid #532D85;
  margin-bottom: 20px;
}
@media (min-width: 769px) {
  .header-sidebar {
    width: 50%;
    border-bottom: none;
  }
}

@media (min-width: 769px) {
  .main_content {
    width: 50%;
  }
}
.main_content > section {
  line-height: 1.5rem;
  margin-bottom: 5rem;
}
.main_content > section h2 {
  color: rgba(239, 239, 239, 0.8);
  text-transform: uppercase;
  margin-bottom: 10px;
}
.main_content > section p {
  margin-bottom: 1rem;
}
@media (min-width: 769px) {
  .main_content > section {
    padding: 0 1rem;
  }
}

.highlight {
  color: rgba(239, 239, 239, 0.9);
  font-weight: 500;
}

a {
  text-decoration: none;
}

/* ABOUT */
/* EXPERIENCE */
.experience_list,
.technologies_list,
.projects_list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.experience_item {
  margin: 1rem 0;
}

.item_box {
  padding: 1rem 0;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
}
.item_box:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.item_box .experience_date {
  margin-bottom: 1rem;
}
@media (min-width: 769px) {
  .item_box {
    padding: 1rem;
    display: flex;
  }
  .item_box .experience_date {
    width: 150px;
    padding-right: 20px;
  }
  .item_box .experience_info {
    width: calc( 100% - 150px);
  }
}

.experience_title .experience_position {
  font-size: 1.2rem;
  color: rgba(239, 239, 239, 0.9);
}
.experience_title .experience_company {
  font-size: 1.1rem;
  color: rgba(239, 239, 239, 0.7);
}

.projects_list .projects_list-item {
  margin-top: 1.5rem;
}
.projects_list .project_name {
  font-size: 1.1rem;
  color: rgba(239, 239, 239, 0.7);
}
.projects_list .projects_links {
  padding: 1rem 0;
  list-style-type: disc;
  list-style-position: inside;
}
.projects_list .projects_links li {
  font-size: 0.9rem;
  color: #87b8bd;
  margin-bottom: 0.2rem;
}
.projects_list .projects_links li a {
  color: #87b8bd;
}

.technologies_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 20px;
}

.technologies_list-item {
  color: #87b8bd;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 0.4rem 0.8rem;
  font-size: 0.8rem;
  line-height: 1rem;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  margin: 0 0.8rem 1rem 0;
  font-weight: 500;
}

.item_box-alternate {
  border: 1px solid rgba(239, 239, 239, 0.5);
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
}

/* MISCELLANEOUS */
@media (min-width: 769px) {
  .miscellaneous_content .item_box-alternate {
    display: flex;
  }
}
.miscellaneous_content .misc_item-image {
  border: 1px solid rgba(239, 239, 239, 0.5);
  margin-right: 1rem;
}
@media (min-width: 769px) {
  .miscellaneous_content .misc_item-info {
    flex-grow: 1;
  }
}

@media (min-width: 769px) {
  .header_sidebar-contents {
    position: fixed;
    padding: 0 1rem;
  }
}

.header-sidebar h1 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.header-sidebar h1 a {
  color: rgba(239, 239, 239, 0.8);
}
.header-sidebar h2 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: rgba(239, 239, 239, 0.7);
}
.header-sidebar .header-tagline {
  font-size: 1rem;
  line-height: 1.5rem;
  color: rgba(239, 239, 239, 0.5);
}
.header-sidebar li {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 769px) {
  .header-sidebar h1 {
    font-size: 3rem;
  }
  .header-sidebar h2 {
    font-size: 1.5rem;
  }
}

.header_navigation {
  display: none;
}
@media (min-width: 769px) {
  .header_navigation {
    display: block;
  }
}
.header_navigation ul {
  margin-top: 4rem;
}
.header_navigation li {
  padding: 0.8rem 0;
}
.header_navigation li a {
  color: rgba(239, 239, 239, 0.7);
}
.header_navigation li a:hover {
  color: rgba(239, 239, 239, 0.9);
}

